	import topBar from '../../components/TopBar.vue'
	import mInput from '../../components/m-input.vue'
	import ButtonItems from "../../components/ButtonItems.vue"
    export default {
		data(){
			return{
				cardsInfo:{},
				cardNumber:'',
				amount:'',
				subType: false,
				balance:this.$route.query.balance
			}
		},
		components: {
			topBar,
			mInput,
			ButtonItems
		},
		onLoad() {
			this.getCardsInfo()	
		},
        methods: {
			setNumber(event){
				if (event > Number(this.balance)){
					this.amount = ''
					this.subType = false
					uni.showToast({
						icon: 'none',
						title: '提现金额输入有误 请从新输入',
					});
					return false
				}
				event === 'all' ? this.amount = this.balance :this.amount = event
				this.subType = true
			},
			getCardsInfo(){
				this.request({
					url: 'consumer/my/bank-cards',
				}).then(res => {
					this.cardsInfo = res.data.result
					let num = res.data.result.cardNumber
					this.cardNumber = num.substr(num.length-4)
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '信息获取失败，请稍后重试',
					});
				})
			},
			submitHandle(){
				if (this.amount > 0){
					uni.navigateTo({url:`/pages/user/confirmWithdrawal?amount=${this.amount}`});
				}
			}
        }
    }
